export const formatTime = (targetTimestamp) => {
  const targetDate = new Date(targetTimestamp);
  const options = {
    year: "numeric",
    month: "2-digit",
    day: "2-digit",
    hour: "2-digit",
    minute: "2-digit",
    // second: "2-digit",
    timeZone: "Asia/Shanghai",
  };
  return `${targetDate.toLocaleString("zh-CN", options)} (UTC+8)`;
};
